// frontend/pages/admin/payments.js
import { useState, useEffect } from "react";
import api from "../../utils/api";

export default function AdminPayments() {
  const [trxID, setTrxID] = useState("");
  const [price, setPrice] = useState("");
  const [msg, setMsg] = useState("");
  const [payments, setPayments] = useState([]);

  // Load all payments
  const loadPayments = async () => {
    try {
      const { data } = await api.get("/api/admin/payment/list");
      setPayments(data);
    } catch (err) {
      console.error(err.response?.data || err.message);
    }
  };

  useEffect(() => {
    loadPayments();
  }, []);

  const addPayment = async () => {
    try {
      const { data } = await api.post("/api/admin/payment/add", { trxID, price });
      setMsg(data.message);
      setTrxID("");
      setPrice("");
      loadPayments(); // refresh list
    } catch (err) {
      console.error(err.response?.data);
      setMsg("❌ Error adding payment");
    }
  };

  const formatDateTime = (isoString) => {
    const date = new Date(isoString);
    return date.toLocaleString(); // Local date & time
  };

  return (
    <div className="p-6 max-w-3xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">Admin – Add bKash Payment</h1>

      <div className="flex mb-4 gap-2">
        <input
          placeholder="trxID"
          value={trxID}
          onChange={(e) => setTrxID(e.target.value)}
          className="border p-2 flex-1 rounded"
        />
        <input
          placeholder="Amount"
          value={price}
          onChange={(e) => setPrice(e.target.value)}
          className="border p-2 w-32 rounded"
        />
        <button onClick={addPayment} className="bg-green-600 text-white px-4 py-2 rounded">
          Add Payment
        </button>
      </div>

      {msg && <p className="mb-4">{msg}</p>}

      <h2 className="text-xl font-semibold mb-2">All Payments</h2>
      <div className="border rounded p-4 overflow-x-auto">
        {payments.length === 0 ? (
          <p>No payments added yet</p>
        ) : (
          <table className="w-full text-left border-collapse">
            <thead>
              <tr className="border-b">
                <th className="py-2 px-2">trxID</th>
                <th className="py-2 px-2">Price</th>
                <th className="py-2 px-2">Status</th>
                <th className="py-2 px-2">Created At</th>
                <th className="py-2 px-2">Updated At</th>
              </tr>
            </thead>
            <tbody>
              {payments.map((p) => (
                <tr key={p._id} className="border-b">
                  <td className="py-2 px-2">{p.trxID}</td>
                  <td className="py-2 px-2">{p.price}</td>
                  <td
                    className={`py-2 px-2 font-semibold ${
                      p.status === "verified" ? "text-green-600" : "text-yellow-600"
                    }`}
                  >
                    {p.status === "verified" ? "Confirmed" : "Pending"}
                  </td>
                  <td className="py-2 px-2">{formatDateTime(p.createdAt)}</td>
                  <td className="py-2 px-2">{formatDateTime(p.updatedAt)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}
